import React from "react";
import "../css/mainPage.css";

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "reactstrap";

export default class Navbar1 extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <Navbar color="white" light expand="lg">
          <NavbarBrand href="/">
            <img
              className="logo"
              src={require("../images/Logo.png")}
              alt="Align Logo"
            />
          </NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <UncontrolledDropdown nav className="dropdown">
                <DropdownToggle nav>Clothing</DropdownToggle>
                <DropdownMenu>
                  <DropdownItem>Tops</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Bottoms</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Dresses</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Outerwear</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Intimates</DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
              <UncontrolledDropdown nav inNavbar className="dropdown">
                <DropdownToggle nav>Accessories</DropdownToggle>
                <DropdownMenu>
                  <DropdownItem>Purses</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Sunglasses</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Jewelery</DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
              <UncontrolledDropdown nav inNavbar className="dropdown">
                <DropdownToggle nav>Shoes</DropdownToggle>
                <DropdownMenu>
                  <DropdownItem>Boots</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Heels/Sandals</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Sneakers</DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
              <UncontrolledDropdown nav inNavbar className="dropdown">
                <DropdownToggle nav>Clearance</DropdownToggle>
                <DropdownMenu>
                  <DropdownItem>Clothing</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Accessories</DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>Shoes</DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}
